// src/components/StockChart.js
import React, { useEffect, useState, useMemo } from 'react';
import { Line } from 'react-chartjs-2';
import {
    Chart as ChartJS,
    LineElement,
    CategoryScale, // x-axis
    LinearScale,   // y-axis
    PointElement,
    Title,
    Tooltip,
    Legend,
} from 'chart.js';
import annotationPlugin from 'chartjs-plugin-annotation';
import Slider from 'react-slider'; // Range slider import
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

// Register required Chart.js components
ChartJS.register(
    LineElement,
    CategoryScale,
    LinearScale,
    PointElement,
    Title,
    Tooltip,
    Legend,
    annotationPlugin
);

const StockChart = ({ stocks }) => {
    const [chartData, setChartData] = useState({});
    const [allDates, setAllDates] = useState([]); // Store all dates
    const [loading, setLoading] = useState(true);
    const [dateRange, setDateRange] = useState([42, 100]); // Initialize range (start and end percentages)

    const [filteredDates, setFilteredDates] = useState([]);
    const [filteredDataSets, setFilteredDataSets] = useState([]);

    const [selectedDate, setSelectedDate] = useState(null); // For the date picker
    const [annotations, setAnnotations] = useState([]); // For the annotations


    // Handle the slider range change
    const handleSliderChange = (newRange) => {
        setDateRange(newRange);
    };

    // Fetch stock data and store dates and closing prices
    // Fetch stock data and store dates and closing prices
    useEffect(() => {
        const fetchData = async () => {
            try {
                const closingPricesDataSet = [];
                let allDatesSet = new Set();

                for (const [stockName, stockData] of Object.entries(stocks)) {
                    const response = await fetch(`${process.env.PUBLIC_URL}/${stockName}.json`);

                    if (!response.ok) {
                        throw new Error(`Error fetching ${stockName}.json: ${response.statusText}`);
                    }

                    const data = await response.json();
                    const dates = Object.keys(data).reverse(); // Oldest to newest

                    // Add dates to the set to get unique dates across all datasets
                    dates.forEach(date => allDatesSet.add(date));

                    // Create a date-to-closing price map for this stock
                    const datePriceMap = {};
                    dates.forEach(date => {
                        datePriceMap[date] = data[date]["4. close"];
                    });

                    closingPricesDataSet.push({
                        label: `${stockName} Closing Price`,
                        dataMap: datePriceMap, // Store the date-price map
                        borderColor: stockData['colour'],
                        fill: false,
                        tension: 0.1,
                        spanGaps: true,
                    });
                }

                // Convert the set of dates to an array and sort in chronological order
                const sortedDates = Array.from(allDatesSet).sort((a, b) => new Date(a) - new Date(b));

                // Map the closing prices data to the sorted dates
                const sortedClosingPricesDataSet = closingPricesDataSet.map(dataset => ({
                    ...dataset,
                    data: sortedDates.map(date => dataset.dataMap[date] || null),
                }));

                setAllDates(sortedDates);
                setFilteredDates(sortedDates);
                setFilteredDataSets(sortedClosingPricesDataSet);
                setChartData({
                    labels: sortedDates,
                    datasets: sortedClosingPricesDataSet,
                });
                setLoading(false);
            } catch (error) {
                console.error('Error loading JSON:', error);
            }
        };

        fetchData();
    }, [stocks]);


    // Update chart data when the range slider changes
    useEffect(() => {
        const [start, end] = dateRange;
        const startIndex = Math.floor((start / 100) * allDates.length);
        const endIndex = Math.floor((end / 100) * allDates.length);

        // Filter dates and datasets within the slider range
        const newFilteredDates = allDates.slice(startIndex, endIndex + 1);
        const newFilteredDataSets = filteredDataSets.map(dataset => ({
            ...dataset,
            data: dataset.data.slice(startIndex, endIndex + 1)
        }));

        setChartData({
            labels: newFilteredDates,
            datasets: newFilteredDataSets,
        });
    }, [dateRange, allDates, filteredDataSets]);

    // Format date helper function
    const formatDate = (date) => {
        const offsetDate = new Date(date.getTime() - date.getTimezoneOffset() * 60000);
        return offsetDate.toISOString().split('T')[0];
    };

    // Chart options
    const options = useMemo(() => ({
        plugins: {
            annotation: {
                annotations: annotations.reduce((acc, date, index) => {
                    acc[`line${index}`] = {
                        type: 'line',
                        xMin: date,
                        xMax: date,
                        borderColor: 'red',
                        borderWidth: 2,
                        label: {
                            content: date,
                            enabled: true,
                            position: 'top',
                        },
                    };
                    return acc;
                }, {}),
            },
        },
    }), [annotations]);

    // Modify addAnnotation function to check for valid date
    const addAnnotation = () => {
        if (selectedDate) {
            let dateStr = selectedDate.toISOString().split('T')[0]; // Format date as 'YYYY-MM-DD'
    
            if (!allDates.includes(dateStr)) {
                // Find the nearest trading date
                const nearestDate = getNearestTradingDate(dateStr);
                alert(`The date ${dateStr} is not a trading date. Annotation will be added to the nearest trading date: ${nearestDate}`);
                dateStr = nearestDate;
            }
    
            // Add the annotation if it's not already added
            setAnnotations((prevAnnotations) => {
                if (!prevAnnotations.includes(dateStr)) {
                    return [...prevAnnotations, dateStr];
                }
                return prevAnnotations;
            });
        }
    };
    

    // Modify addAnnotationsEveryYear function to include only valid dates
    const addAnnotationsEveryYear = () => {
        if (selectedDate && allDates.length > 0) {
            const selectedMonthDay = selectedDate.toISOString().split('T')[0].slice(5); // Get 'MM-DD'
            const years = new Set(allDates.map(date => date.slice(0, 4))); // Extract years from dates
    
            // Generate dates and adjust them to nearest trading dates
            const datesForAnnotations = Array.from(years).map(year => {
                const dateStr = `${year}-${selectedMonthDay}`;
                if (allDates.includes(dateStr)) {
                    return dateStr;
                } else {
                    // Adjust to nearest trading date
                    return getNearestTradingDate(dateStr);
                }
            });
    
            // Remove duplicates in case multiple dates adjusted to the same trading date
            const uniqueDatesForAnnotations = [...new Set(datesForAnnotations)];
    
            // Add annotations
            setAnnotations((prevAnnotations) => {
                const newAnnotations = uniqueDatesForAnnotations.filter(date => !prevAnnotations.includes(date));
                return [...prevAnnotations, ...newAnnotations];
            });
        }
    };
    

    // Helper function to find the nearest trading date
const getNearestTradingDate = (dateStr) => {
    // Convert the date string to a Date object
    const date = new Date(dateStr);

    // Convert allDates to Date objects
    const tradingDates = allDates.map(d => new Date(d));

    // Find the closest date
    let closestDate = tradingDates.reduce((prev, curr) => {
        return Math.abs(curr - date) < Math.abs(prev - date) ? curr : prev;
    });

    // Convert back to date string
    return closestDate.toISOString().split('T')[0];
};


    return (
        <div>
            {loading ? (
                <p>Loading chart...</p>
            ) : (
                <>
                    <Line data={chartData} options={options} />

                    {/* Controls Container */}
                    <div style={{ marginTop: '20px' }}>

                        {/* Date Range Slider */}
                        <div style={{ marginBottom: '20px' }}>
                            <label style={{ display: 'block', marginBottom: '10px' }}>Select Date Range:</label>
                            <Slider
                                value={dateRange}
                                onChange={handleSliderChange}
                                min={0}
                                max={100}
                                step={1}
                            />
                        </div>

                        {/* DatePicker and Buttons */}
                        <div>
                            <div style={{ marginBottom: '10px' }}>
                                <DatePicker
                                    selected={selectedDate}
                                    onChange={(date) => setSelectedDate(date)}
                                    dateFormat="yyyy-MM-dd"
                                    placeholderText="Select a date"
                                />
                            </div>
                            <div style={{ display: 'flex', gap: '10px', flexWrap: 'wrap' }}>
                                <button onClick={addAnnotation}>Add Annotation</button>
                                <button onClick={addAnnotationsEveryYear}>Add Annotation Every Year</button>
                                <button onClick={() => setAnnotations([])}>Clear Annotations</button>
                            </div>
                        </div>
                    </div>
                </>
            )}
        </div>
    );
};

export default StockChart;
